// NPM Dependencies
import React, { Component } from 'react';
import Sarus from '@anephenix/sarus';

// File Dependencies
import '../styles/theme.scss';
import '../styles/WebSocketForm.scss';

class WebSocketForm extends Component {
  constructor(props) {
    super(props);
  }

  connect(event) {
    event.preventDefault();
    console.log('Called connection');
  }

  render() {
    return (
      <div id="websocket-form">
        <form onSubmit={this.connect}>
          <label htmlFor="websocket-server">WebSocket server url</label>
          <input
            width="250"
            type="text"
            placeholder="e.g. wss://ws.anephenix.com"
            name="websocket-server"
          />
          <button type="submit">Connect</button>
        </form>
      </div>
    );
  }
}

class HomePage extends Component {
  render() {
    return (
      <div>
        <WebSocketForm />
      </div>
    );
  }
}

export default HomePage;
